s monetarias expansivas conducen a una mala asignación de recursos y a un sobreendeudamiento, lo que inevitablemente desemboca en recesiones profundas cuando el mercado intenta corregir los desequilibrios generados.")])])],1)]),_vm._m(16),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slide-1-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Joseph Schumpeter")]),_c('p',[_vm._v("Economista de origen austríaco, propuso una visión dinámica del capitalismo en la que el cambio y la transformación son elementos esenciales para el desarrollo de los mercados. En su obra Capitalismo, socialismo y democracia (1942), Schumpeter introdujo el concepto de “destrucción creativa”, que describe el proceso mediante el cual las innovaciones tecnológicas y organizativas reemplazan estructuras económicas obsoletas, generando crecimiento y eficiencia (Godelier, 2012).")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slide-2-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Escuela neoschumpeteriana")]),_c('p',[_vm._v("Expande esta idea al considerar que la innovación no solo se limita al ámbito tecnológico, sino que también incluye cambios en los modelos de negocio, en las estructuras de mercado y en la gestión empresarial. Desde esta perspectiva, el progreso económico no ocurre de manera uniforme, sino a través de ciclos de disrupción y adaptación. Los avances en industrias como la informática, la biotecnología y la energía renovable han demostrado que la destrucción creativa sigue siendo un fenómeno clave en la evolución de la economía moderna.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slide-3-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Papel del emprendedor")]),_c('p',[_vm._v("Para Schumpeter, el papel del emprendedor es fundamental en este proceso, porque es el agente que introduce innovaciones y desafía el statu quo. A diferencia de las teorías económicas tradicionales, que consideran a las empresas como entidades que buscan maximizar beneficios dentro de un entorno estable, la visión neoschumpeteriana sostiene que las empresas más exitosas son aquellas que logran reinventarse constantemente y adaptarse a los cambios del mercado.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slide-4-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("nversión en investigación y desarrollo (I+D)")]),_c('p',[_vm._v("Los neoschumpeterianos también destacan la importancia de la inversión en investigación y desarrollo (I+D) como factor determinante del crecimiento a largo plazo. En un mundo donde la tecnología avanza rápidamente, las economías que promueven la innovación y el desarrollo científico tienden a generar mayor riqueza y bienestar. Esto ha llevado a que muchos países implementen políticas de estímulo a la innovación, como incentivos fiscales para la inversión en tecnología y la creación de ecosistemas de emprendimiento.")])])])])],1)]),_vm._m(17),_vm._m(18),_vm._m(19),_vm._m(20),_c('p',[_vm._v("A lo largo del tiempo, la economía ha evolucionado incorporando enfoques que van más allá de los modelos tradicionales basados en la racionalidad absoluta de los agentes económicos.")]),_c('div',{staticClass:"bg-full-width bg-color-info"},[_c('div',{staticClass:"px-4 p-md-5"},[_c