ica evolucionó a partir del pensamiento clásico, introduciendo un análisis más detallado del comportamiento individual y los mecanismos de toma de decisiones económicas. Mientras que los economistas clásicos se centraban en la producción y la distribución, los neoclásicos pusieron énfasis en el estudio del consumo y la formación de precios en los mercados. Esta nueva perspectiva llevó a la formulación de modelos matemáticos más precisos para explicar la interacción entre consumidores y productores.")])]),_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/tema1/5.svg")}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema1/6.svg"),"alt":"","data-aos":"zoom-in"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-color-5 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("A pesar de sus diferencias, tanto la escuela clásica como la neoclásica, compartían la creencia en la eficiencia de los mercados y en la capacidad del sistema de precios para regular la economía de manera óptima. Sin embargo, los neoclásicos avanzaron en el análisis matemático y en la formulación de modelos formales para describir el comportamiento económico, sentando las bases del pensamiento económico moderno y facilitando el desarrollo de herramientas como el equilibrio general y la teoría del consumidor.")])])]),_c('div',{staticClass:"col-lg-auto"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/7.png"),"alt":""}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{s